class MorePublish extends Component {

    state = { showContactUs: false, type: "" }

    clickToShowContact = (status, type) => event => {
        this.setState({
            showContactUs: status,
            type
        })
    }
    render() {
        const { showContactUs, type } = this.state
        const { userData } = this.props;
        const authData = userData?.user_data?.sessionData
        const websiteUrl = authData?.createFrom.toLowerCase() == 'shopify' ? `shopify` : authData?.createFrom.toLowerCase() == 'bigcommerce' ? `bigcommerce` : 'publish'

        return (<>
            <div className="subheader">
                <PublishSubheader websiteUrl={websiteUrl} />
            </div>
            <div className='d-flex flex-column flex-column-fluid full-content'>
                <Container>
                    <Row className='gx-3 w-100'>
                        {MoreButtons?.length ? MoreButtons.map((Item, Index) =>
                            <Col sm={6} md={3} key={`more_section${Index}`}>
                                <Card className="card-stretch gutter-b cursor-pointer" onClick={this.clickToShowContact(true, Item.title)}>
                                    <Card.Body className='flex-column px-4'>
                                        <Image className="mb-3 h-50px w-auto object-fit-contain" src={baseUrl(`media/icons/more-publish/${Item.icon}.svg`)} height={50} width={Item.id == 4 ? 101 : 50} alt={Item.title} fluid />
                                        <h5 className='fs-6'>{Item.title}</h5>
                                        <p>{Item.text}</p>
                                    </Card.Body>
                                </Card>
                            </Col>
                        ) : null}
                    </Row>
                    <ContactModal type={type} show={showContactUs} onCloseModel={this.clickToShowContact(false, "")} />
                </Container>
            </div>
        </>
        )
    }
}

export default withRouter(MorePublish)
